
ul {
    width: auto;
    padding:10px;
    display: grid;
    height: 200vh;
    overflow: hidden;
}

li {
    z-index: 1;
    list-style: none;
    text-align: center;
    padding: 2px;
    display: list-item;
}

a {
    display: block;
    text-align: center;
    width: 100%;
    font-size: 6vw;
    text-decoration: none;
    color: rgb(211, 211, 211);
    text-shadow: 3px 3px #2b2b2b;
}

@media (max-width: 1024px) {
    ul {
        display: block;
    }

    li {
        text-align: center;
        padding-top: 8vw;
        padding-bottom: 8vw;
        transform: translateX(0px)!important;
    }

    a {
        font-size: 11vw;
        background-color: rgba(0, 0, 0, 0.5);
    }

    img {
        position: absolute;
    }
}
@media (min-width: 1024px) {
    /*a:hover {
        background-color: rgba(0, 0, 0, 0.5);
    }*/
    
    a:hover img {
        transition: opacity 0.3s ease-in-out 0.1s;
        opacity: 100%;
    }
}


img {
    z-index: 0;
    transition: opacity 0.3s ease-in-out 0.1s;
    opacity: 0%;
    width: 9rem;
    padding-left: 2vw;
    margin-top: 2vw;
}